<template>
  <div class="alert-container" v-if="isShow">
    <div class="alert-div">
      <div class="content-div">
        <div class="title-text" v-if="title !== null">{{title}}</div>
        <div class="content-text" v-if="content !== null">{{content}}</div>
        <slot></slot>
      </div>
      <div class="button-div">
        <div class="cancel" v-if="!hideCancel" @click="onCancel">
          {{cancelText}}
        </div>
        <div class="line"/>
        <div class="confirm" @click="onConfirm">
          {{confirmText}}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
/**
 * 全局统一弹窗
 */
export default {
  name: 'CommonAlert',
  props: {
    title: {
      type: String,
      default: null,
    }, // 标题文本
    content: {
      type: String,
      default: null,
    }, // 内容文本
    confirmText: { // 确定按钮文本
      type: String,
      default: '确定',
    },
    cancelText: { // 取消按钮文本
      type: String,
      default: '取消'
    },
    hideCancel: { // 隐藏【取消】按钮
      type: Boolean,
      default: false
    },
    showOnConfirm: { // 点击确定按钮时是否继续显示弹窗
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      isShow: true, // 是否显示
    };
  },
  methods: {
    /**
     * 确定回调
     */
    onConfirm () {
      !this.showOnConfirm && this.hideAlert();
      this.$emit('onConfirm');
    },
    /**
     * 取消回调
     */
    onCancel () {
      this.hideAlert();
      this.$emit('onCancel');
    },
    /**
     * 隐藏弹窗
     */
    hideAlert () {
      this.isShow = false;
    }
  }
}
</script>
